
.calendar {
  float: left;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.calendar+.calendar{
  border-left: solid 1px #eaeaea;
  margin-left: 5px;
  padding-left: 5px;
}
.calendar-head {
  line-height: 34px;
  height: 34px;
  text-align: center;
  position: relative;
}
.calendar-head a {
  color: #666;
  font-weight: bold;
  cursor: pointer;
  display: inline-block;
  text-align: center;
  position: absolute;
  padding: 0 5px;
  font-size: 16px;
}
.calendar-head a:hover {
  color: #1284e7;
}
.calendar-head .calendar-year-select,
.calendar-head .calendar-month-select {
  font-size: 12px;
  padding: 0 2px;
  position: relative;
}
.calendar-prev-decade-btn,
.calendar-prev-year-btn {
  left: 6px;
}
.calendar-prev-month-btn {
  left: 24px;
}
.calendar-next-decade-btn,
.calendar-next-year-btn {
  right: 6px;
}
.calendar-next-month-btn {
  right: 24px;
}
.calendar-body {
  position: relative;
  width: 196px;
  height: 196px;
}
.calendar-days {
  width: 100%;
  height: 100%;
}
.calendar-week,
.calendar-date {
  font-weight: normal;
  width: 14.28%;
  height: 14.28%;
  text-align: center;
  box-sizing: border-box;
  overflow: hidden;
  float: left;
}
.calendar-week:before,
.calendar-date:before {
  content: "";
  display: inline-block;
  height: 100%;
  vertical-align: middle;
}
.calendar-date {
  cursor: pointer;
}
.calendar-date-out {
  color: #ccc;
}
.calendar-date:hover,
.calendar-date-on {
  background: #eaf8fe;
}
.calendar-date-selected,
.calendar-date-selected:hover {
  color: #fff;
  font-weight: bold;
  background: #1284e7;
}
.calendar-date-disabled {
  cursor: not-allowed !important;
  color: #bcbcbc !important;
  background: #f3f3f3 !important;
}
.calendar-foot {
  margin-top: 5px;
}
.calendar-hour {
  display: inline-block;
  border: 1px solid #e6e5e5;
  color: #9e9e9e;
}
.calendar-hour a {
  display: inline-block;
  padding: 2px 4px;
  cursor: pointer;
}
.calendar-hour a:hover,
.calendar-hour a.on {
  color: #1284e7;
}
.calendar-years,
.calendar-months,
.calendar-hours,
.calendar-minutes,
.calendar-seconds {
  width: 100%;
  height: 100%;
  position: absolute;
  background: #fff;
  left: 0;
  top: 0;
}
.calendar-months a {
  width: 33.33%;
  height: 25%;
}
.calendar-years a {
  width: 33.33%;
  height: 25%;
}
.calendar-hours a {
  width: 20%;
  height: 20%;
}
.calendar-minutes a,
.calendar-seconds a {
  width: 16.66%;
  height: 10%;
}
.calendar-title {
  margin-top: -30px;
  height: 30px;
  line-height: 30px;
  background: #fff;
  text-align: center;
  font-weight: bold;
}



